    getCurrentQuestionItem(){
      this.currentQuestionItem = this.question_list[this.currentQuestionIndex]
      this.begin_time_long_sec = Math.ceil(new Date().getTime()/1000)
    },
    saveCodeOnly(){
      let self = this
      let questionComponent = this.$refs.questionItem
      let answer = questionComponent.getAnswer()
      let question_answer = {
        question_id:self.currentQuestionItem.question_id,
        question_type:self.currentQuestionItem.question_type,
        ...answer
      }
      let params = {
        inclass_task_record_id:self.inclass_task_record_id,
        question_answer
      }
      console.log(params,'autoSaveActions');
      this.autoSaveActions(params).then((res)=>{
        this.loading = false
        let { error_code, data } = res;
        if(!error_code){}
      })
    },
    submit(){
      let self = this
      if(self.loading || self.waiting){
        return
      }
      if(!this.isExistQuestionComponent){
        this.submitNotExist()
        return
      }
      let questionComponent = this.$refs.questionItem
      // console.log(questionComponent,111);
      let isAnswered = questionComponent.isAnswered()
      // console.log(isAnswered,1122);
      if(isAnswered){
        let answer = questionComponent.getAnswer()
        let question_answer = {
          question_id:self.currentQuestionItem.question_id,
          question_type:self.currentQuestionItem.question_type,
          ...answer
        }
        console.log(question_answer,33333333);
        // debugger
        this.submitQuestion(question_answer)
      }
    },
    submitNotExist(){
      if(this.currentQuestionIndex === this.question_list.length - 1){
        this.$message.warning('存在未适配的题型，无法提交')
      }else{
        this.loading = true
        this.waiting = true
        this.nextQuestionDo()
      }
    },
    //是否已答，用于提交按钮置灰或者亮显
    getIsAnswered(nv){
      this.isAnswered = nv
    },
    checkAnswer(question_answer,cb){
      let self = this
      return new Promise((resolve,reject)=>{
        let params = {
          inclass_task_record_id:self.inclass_task_record_id,
          begin_time_long_sec:self.begin_time_long_sec,
          end_time_long_sec:self.end_time_long_sec,
          question_answer
        }
        console.log(params);
        self.loading = true
        self.waiting = true
        //编程题的loading是额外样式
        if(question_answer.question_type == 16){
          self.checkingCodeDialog.visible = true
        }
        self.checkAnswerActions(params).then((res)=>{
          self.loading = false
          self.checkingCodeDialog.visible = false
          let { error_code, data } = res;
          if(!error_code){
            let {check_answer_result,gold_reward,gold_total} = data
            let correct = self.isCorrect(check_answer_result)
            if(correct){
              //右上角金币直接赋值成服务端给的全部金币
              //如果不是编程题，金币动画完成后再回显金币,如果是先用临时tempGoldOfLesson接一下，动画完成后再复制给this.homeData.gold_of_lesson
              if(self.currentQuestionItem.question_type == 16){
                self.homeData.gold_of_lesson = gold_total
              }else{
                self.tempGoldOfLesson = gold_total
              }
              self.dialogData = {
                visible:true,
                msg:'恭喜，答对啦！',
                gold:gold_reward,
                icoType:'right',
                showGoldAnimation:self.currentQuestionItem.question_type == 16?false:true
              }
            }else{
              let msg = self.currentQuestionItem.question_type == 16 ? '发现问题！' : '很遗憾，答错了~'
              let subMsg = self.currentQuestionItem.question_type == 16 ? '请检查修正后再提交~！' : ''
              self.dialogData = {
                visible:true,
                msg,
                subMsg,
                icoType:'wrong'
              }
            }
            resolve(correct)
            // cb && cb(correct)
            // cb && cb(true)
          }else{
            reject()
          }
        },()=>{
          self.loading = false
          self.waiting = false
          self.checkingCodeDialog.visible = false
          reject()
        })
      })
    },
    submitQuestion(question_answer){
      let self = this
      this.end_time_long_sec = Math.ceil(new Date().getTime()/1000)
      console.log(this.begin_time_long_sec,this.end_time_long_sec,`耗时:${this.end_time_long_sec - this.begin_time_long_sec}`);
      this.checkAnswer(question_answer).then((correct)=>{
        //编程题答错后开始时间重新算
        if(self.currentQuestionItem.question_type == 16 && !correct){
          self.begin_time_long_sec = Math.ceil(new Date().getTime()/1000)
        }
        //如果最后一题就提交，不是就下一题
        let isProgrammingType = this.currentQuestionItem.question_type == 16
        if(self.currentQuestionIndex === this.question_list.length - 1){
          !isProgrammingType ? self.submitTask() : correct && self.submitTask()
        }else{
          !isProgrammingType ? self.nextQuestionDo() : correct && self.nextQuestionDo()
        }
      },()=>{
        //如果请求异常开始时间都重新算
        self.begin_time_long_sec = Math.ceil(new Date().getTime()/1000)
      })
    },
    nextQuestionDo(){
      let nextQ = ()=>{
        this.loading = false
        this.currentQuestionIndex ++
        let expires = fun.getTomorrowTime()
        //题型全部有适配才把做过的题号存缓存，否则不存
        !this.hasNoExistQuestionType && helper.cookieUtils.setItem(this.currentQuestionIndexKey,this.currentQuestionIndex,expires)
        this.componentShow = false
        this.$nextTick(()=>{
          this.waiting = false
          this.isAnswered = false
          this.begin_time_long_sec = 0
          this.end_time_long_sec = 0
          this.getCurrentQuestionItem()
          this.componentShow = true
        })
      }
      if(!this.isExistQuestionComponent){
        setTimeout(()=>{
          nextQ()
        },2500)
      }else{
        this.nextQuestionDoInterval = setInterval(()=>{
          if(!this.dialogData.visible){
            this.$nextTick(()=>{
              setTimeout(()=>{
                nextQ()
              },500)
            })
            if(this.nextQuestionDoInterval){
              clearInterval(this.nextQuestionDoInterval)
              this.nextQuestionDoInterval = null
            }
          }
        },1000)
      }
    },
    submitTask(){
      let self = this
      if(this.hasNoExistQuestionType){
        this.$message.warning('存在未适配的题型，无法提交')
        return
      }
      let params = {
        inclass_task_record_id:self.inclass_task_record_id,
        submit_by_system:false
      }
      this.loading = true
      this.waiting = true
      this.submitTaskActions(params).then((res)=>{
        this.loading = false
        let { error_code, data } = res;
        if(!error_code){
          //1秒轮询一次，发现金币弹窗消失了再完全锁屏
          this.submitTaskInterval = setInterval(()=>{
            if(!this.dialogData.visible){
              this.waiting = false
              setTimeout(()=>{
                this.lockScreenAndInit()
              },500)
              if(this.submitTaskInterval){
                clearInterval(this.submitTaskInterval)
                this.submitTaskInterval = null
              }
            }
          },1000)
        }
      },()=>{
        this.loading = false
        this.waiting = false
      })
    },
    lockScreenAndInit(){
      // this.worker && this.worker.terminate()
      // this.worker && this.worker.removeEventListener('message',this.renewOnline)
      //回到锁屏状态
      this.lockDialog.visible = true
      //清除试卷信息，如果有重新下发会重新调用接口获取
      this.examination_paper = {}
      //答题答完隐藏题目,清空当前题目相关
      this.currentQuestionItem = {}
      this.question_list = []
      helper.cookieUtils.removeItem(this.currentQuestionIndexKey)
      this.currentQuestionIndex = 0
      this.componentShow = false
      this.isAnswered = false
      this.begin_time_long_sec = 0
      this.end_time_long_sec = 0
      this.homeData.inclass_task_id = 0
    },
    renewOnline(e){
      //测试代码
      // this.count = this.count || 0
      // this.count ++
      // if(this.count >= 3 && this.count < 7){
      //   e.data.inclass_task_id = 66666
      // }else if(this.count >= 7 && this.count < 10){
      //   e.data.inclass_task_id = null
      // }else if(this.count >= 10){
      //   e.data.inclass_task_id = 66666
      // }
      this.not_done_flag = e.data.not_done_flag
      // this.gold_of_lesson_student = e.data.gold_of_lesson_student
      this.homeData.gold_of_lesson = e.data.gold_of_lesson_student
      if(this.not_done_flag){
        //轮询如果重新下发了任务，题目要重新展示出来
        /*if(this.examination_paper.examination_id){
          this.lockDialog.visible = false
          this.componentShow = true
        }else{
          this.homeData.inclass_task_id = e.data.inclass_task_id
          this.currentQuestionIndexKey = `currentQuestionIndex-${this.homeData.student_id}-${this.homeData.inclass_task_id}`
          this.getEnterInclassTask(()=>{
            this.lockDialog.visible = false
            this.componentShow = true
          })
        }*/
        if(!this.homeData.inclass_task_id && e.data.inclass_task_id){
          this.homeData.inclass_task_id = e.data.inclass_task_id
          this.currentQuestionIndexKey = `currentQuestionIndex-${this.homeData.student_id}-${this.homeData.inclass_task_id}`
          this.getEnterInclassTask(()=>{
            this.lockDialog.visible = false
            this.componentShow = true
          })
        }
      }else{
        this.lockDialog.visible = true
        this.componentShow = false
        this.homeData.inclass_task_id = 0
      }
    },
    setWebWorker(){
      let self = this
      let urlPathBase = window.location.protocol + "//" + window.location.host;
      this.worker = new Worker(`${urlPathBase}/static/worker/renewOnline.js`);
      this.worker.addEventListener('message',this.renewOnline)
      let arrangement_id = this.$route.query.arrangement_id
      let class_id = this.$route.query.class_id
      this.worker.postMessage({
        token:self.info.token,
        pathApi:process.env.VUE_APP_SERVER2,
        course_arrangement_id:arrangement_id,
        class_id
      })
    }
  },
  mounted() {
    console.log(process.env,11);
    let _this = this
    window.addEventListener('resize',_this.setRootFontSize)
    this.getHomeData()
    this.setWebWorker()
  },
  computed: {
    ...mapGetters('account', ['info']),
    //当前题型是否是未配置的题型
    isExistQuestionComponent(){
      let questionMapperKeys = Object.keys(questionMapper)
      let currentQuestionItem = this.currentQuestionItem
      if(!currentQuestionItem || !currentQuestionItem.question_type){
        return false
      }else{
        return questionMapperKeys.indexOf(currentQuestionItem.question_type) > -1
      }
    }
  },
  components: {
    // ...QuestionReader,
    virDialog,
    codeChecking,
    lockDialog
  },
  created() {},
  beforeRouteEnter(to, from, next) {
    document.title = '虚拟教室'
    next((vm) => {
      vm.setRootFontSize();
    });
  },
  destroyed() {
    let _this = this
    document.title = '九色鹿学生端'
    let html = document.getElementsByTagName("html")[0];
    html.style.fontSize = "";
    document.body.style.fontSize = ''
    window.removeEventListener('resize',_this.setRootFontSize)

    //清空worker相关
    this.worker && this.worker.removeEventListener('message',this.renewOnline)
    this.worker && this.worker.terminate()
  },
  watch: {

  },
};
</script>

