      var self = this;
      var showGoldAnimation = self.dialogData.showGoldAnimation;
      var goldAnimationDom = document.querySelector('.gold-child');

      if (showGoldAnimation && goldAnimationDom) {
        goldAnimationDom.removeEventListener('webkitAnimationEnd', self.animationEnd);
        goldAnimationDom.addEventListener('webkitAnimationEnd', self.animationEnd);
      }
